.menu {
  list-style: none;
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  box-sizing: border-box;
  margin: 0;
  padding: 1rem 1rem 0 1rem;
  width: 20.5rem;
  background: #f0f8ff1a;
  border-radius: 10px;
  text-align: center;
  display: flex;
  flex-wrap: wrap;

  .nope {
    color: tomato;
  }

  .menu-item {
    margin-bottom: 1rem;

    #scale-range {
      margin-left: 1rem;
    }

    &.play-pause {
      flex-basis: calc(50% - 1rem);
      margin-right: 1rem;
    }

    .btn-play-pause,
    .btn-error {
      width: 100%;
    }
  }
}
